@import './../../styles/breakpoints';

.header {
  &-news {
    height: 51px;
    position: sticky;
    top: 51px;
    // top: 51px !important;
    z-index: 1;
    background-color: #ffffff;
    box-shadow: 0 7px 28px 0 rgba(0, 0, 0, 0.15);

    .link {
      letter-spacing: 1px;
      text-transform: uppercase;
      color: #949494;

      &:hover,
      &.active {
        color: #2A2A2A;
      }

      &.active {
        border-bottom: 2px solid #13A5F7;
      }
    }

    &-text {
      font-size: 11px;
      font-weight: 500;
      letter-spacing: 1px;
      padding-left: 20px;
    }

    .close-icon {
      path {
        stroke: black;
      }
    }
  }

  &-loading {
    display: block;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 3px;
    background: #13a5f7;
    transform: translate(-100%);
    transition: transform 0.2s ease-out;
  }

  &-loading-wrapper {
    overflow: hidden;
    position: absolute;
    height: 3px;
    width: 100%;
    bottom: -3px;
    left: 0;

    @media (min-width: $breakpoint-tablet-sm) {
      bottom: 0;
    }

    .menu-opened & {
      display: none;
    }
  }
}