@import './../../styles/breakpoints';

.profile-proposed-articles {
  padding: 33px 20px;

  &-header {
    font-size: 14px;
    font-weight: 900;
    letter-spacing: 7px;
    color: #2a2a2a;
    text-transform: uppercase;
    line-height: 1.76;
  }

  &-btn {
    font-size: 16px;
    font-weight: bold;
    line-height: 2;
    letter-spacing: normal;
    color: #ffffff;
    background-color: #13a5f7;
    border: 0;
    width: 100%;

    @media (min-width: $breakpoint-tablet-sm) {
      width: auto;
    }
  }

  .close-icon {
    cursor: pointer;
  }
}