@import './../../styles/shared';
@import './../../styles/breakpoints';

.news-feed {
  &.collapsed {
    width: 240px;
  }

  &.expanded {
    width: 54px;
    height: 100%;
    background-color: #F9F9F9;

    .news-feed-title {
      white-space: nowrap;
      margin-top: 180px;
      transform: rotate(-90deg);
    }
  }

  &-sticky {
    position: sticky;
    top: 0;
    z-index: 1;
    box-shadow: 0 7px 28px 0 rgba(0, 0, 0, 0.15);
  }

  &-title {
    line-height: 14px;
    font-size: 14px;
    letter-spacing: 4px;
    text-transform: uppercase;
    color: #2A2A2A;

    &-sticky {
      height: 51px;

      .sticky & {
        box-shadow: 0 7px 28px 0 rgba(0, 0, 0, 0.15);
        background-color: #ffffff;
      }
    }
  }

  &-list {
    border-right: 1px solid #efefef;
  }

  &-add {
    width: 40px;
    height: 40px;
    background-color: #fff;
    position: absolute;
    z-index: 3;
    font-size: 20px;
    color: #36b3f8;
    right: 15px;
    top: 82px;

    &:hover {
      cursor: pointer;
    }

    &-short {
      left: 5px;
    }
  }

  &-item {
    border-bottom: 1px solid #EFEFEF;
    color: #2A2A2A;

    &:hover {
      color: #13A5F7;
      cursor: pointer;
    }

    .date {
      line-height: 12px;
      font-size: 12px;
      text-transform: uppercase;
      color: #13A5F7;
    }

    .title {
      font-family: Georgia;
      font-size: 16px;

      .test-1 & {
        font-family: 'Merriweather', serif;
        font-weight: 300;
      }

      .test-2 & {
        font-family: 'EB Garamond', serif;

        font-weight: 400;
      }

      .test-3 & {
        font-family: 'Vollkorn', serif;

        font-weight: 400;
      }

      .test-4 & {
        font-family: 'Alegreya', serif;

        font-weight: 400;
      }

      .test-5 & {
        font-family: 'Cormorant Garamond', serif;

        font-weight: 300;
      }

      .test-6 & {
        font-family: 'IBM Plex Serif', serif;

        font-weight: 300;
      }

      .test-7 & {
        font-family: 'Cormorant', serif;

        font-weight: 300;
      }

      .test-8 & {
        font-family: 'Spectral', serif;

        font-weight: 300;
      }

      .test-9 & {
        font-family: 'Alegreya SC', serif;

        font-weight: 400;
      }

      .test-10 & {
        font-family: 'Cormorant Infant', serif;

        font-weight: 300;
      }

      .test-11 & {
        font-family: 'Spectral SC', serif;
        font-weight: 300;
      }


    }
  }

  &-mobile {
    background-color: #f9f9f9;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;

    .sticky {
      z-index: 1;
    }

    .close-icon-wrapper {
      path {
        stroke: #3c3c3c;
      }
    }
  }
}